
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'InputWrapper',
  props: {
    id: {
      type: [String, Number],
      required: true,
    },
    description: {
      type: String,
      default: null,
    },
    hasErrors: {
      type: Boolean,
      default: null,
    },
    errorMessage: {
      type: String,
      default: null,
    },
    hideLabel: {
      type: Boolean,
      default: false,
    },
    label: {
      type: String,
      required: true,
    },
  },
  computed: {
    idFormatted(): string {
      return 'base-input--' + this.id;
    },
    helperIdFormatted(): string | null {
      if (this.description || (this.hasErrors && this.errorMessage)) {
        return 'base-input-helper--' + this.id;
      }
      return null;
    },
  },
});
