
import { defineComponent } from 'vue';
import BaseIcon from '@/components/base/BaseIcon.vue';
import IconArrowRight from '@/components/icons/IconArrowRight.vue';

export default defineComponent({
  name: 'QuickReadButton',
  emits: ['click'],
  components: {
    BaseIcon,
    IconArrowRight,
  },
  props: {
    includeText: {
      type: Boolean,
      default: true,
    },
    imageSrc: {
      type: String,
      required: false,
    },
  },
});
