
import { defineComponent } from 'vue';
import icons from '@/components/icons/all';
import BaseIcon from '@/components/base/BaseIcon.vue';

export default defineComponent({
  components: {
    BaseIcon,
  },

  computed: {
    iconNames() {
      return Object.keys(icons);
    },
    iconComponents() {
      return Object.values(icons);
    },
  },
});
